import * as webpack from 'webpack';
import * as path from 'path';
import * as gulp from 'gulp';
import * as through2 from 'through2';
import * as gulpUtil from 'gulp-util';
import * as merge from 'merge2';
import * as gulpReplace from 'gulp-replace';
import setWebpackPublicPath = require('@microsoft/loader-set-webpack-public-path');
import { IClientSideComponentManifest } from '@microsoft/sp-module-interfaces';
import { escapeRegExp } from 'lodash';

import {
  IComponentModuleConfiguration,
  IPathModuleConfiguration,
  ILocalizedPathModuleConfiguration
} from '@microsoft/sp-module-interfaces';

import { IExternalProject } from './../configureWebpack/ExternalsProcessor';
import OdspGulpTask, { ILocaleMap, INonAMDExternalModuleConfig } from './../OdspGulpTask';
import { IConfigEntry, IBundleEntry, getBundleId } from './../configureWebpack/ConfigureWebpackTask';
import constants from '../constants';
import { isUrl } from '../utilities/url';

// If we change the type of bundle we're producing, we need to update this
const escapedStringsName: string = escapeRegExp(constants.stringsExternalBundleName);
const defaultStringsModuleRegex: RegExp =
  new RegExp(`^(define\\(.*\\[.*(\\"|\\'))(?:${escapedStringsName})((?:\\2).*\])`);

export const externalsRegex: RegExp = /external \"(.+)\"$/;

export interface ICopyAssetsTaskConfig {
  /**
   * Path to where assets should be staged for deployment to CDN during a production build
   */
  deployCdnPath?: string;

  /**
   * Extensions of webpack-produced assets to ignore
   */
  extsToIgnore?: string[];
}

export interface IWebpackStatsObject {
  assets: IWebpackAsset[];
  chunks: IWebpackChunk[];
  modules: IWebpackModule[];
}

export interface IWebpackAsset {
  name: string;
  chunks: number[];
}

export interface IWebpackChunk {
  entry: boolean;
  id: number;
  files: string[];
  names: string[];
  hash: string;
  size: number;
}

export interface IWebpackModule {
  name: string;
  chunks: number[];
}

interface IExtendedWebpackModule extends IWebpackModule {
  externalName?: string;
  renameCallbacks?: ((filename: string) => void)[];
  configExternalFullPath?: string;
  isExternalUrl: boolean;
  nonAmdConfig: INonAMDExternalModuleConfig;
}

interface ITokenReplace {
  oldValueRegex: RegExp;
  newValue: string;
}

export interface IDeployEntry {
  manifest: IClientSideComponentManifest;
  bundleEntry: IBundleEntry;
  configEntry: IConfigEntry;
  entryName?: string;
  debugModules?: IDeployModules;
  productionModules?: IDeployModules;
}

export interface IDeployModules {
  entryModuleId?: string;
  localizedPathModules: Map<string, ILocalizedPathModuleConfiguration>;
  pathModules: Map<string, IPathModuleConfiguration>;
  componentModules: Map<string, IComponentModuleConfiguration>;
}

interface IStringsModule {
  path: string;
  gulpStream: NodeJS.ReadWriteStream;
}

interface IDefaultLocale {
  defaultLocale: string;
  defaultLocaleInPaths: boolean;
}

/**
 * Prepares deployment to CDNs and the manifest server through onedrive-buildtools.
 *
 * Example:
 *  IN:
 *   webpack stats
 *   deployCdnPath - a path where files should be placed for upload to CDNs and the manifest server
 *
 *  OUT:
 *   all assets for the package copied to {deployCdnPath}
 *   all externals for the package copied to {deployCdnPath}
 *   data required to produce manifests in the properties object
 */
export class CopyAssetsTask extends OdspGulpTask<ICopyAssetsTaskConfig> {
  public name: string = 'copy-assets';
  public taskConfig: ICopyAssetsTaskConfig = {
    deployCdnPath: undefined,
    extsToIgnore: ['.map', '.stats.json', '.stats.html']
  };

  public loadSchema(): Object {
    return require('./copy-assets.schema.json');
  }

  public executeTask(gulp: gulp.Gulp, completeCallback: (error?: string) => void): NodeJS.ReadWriteStream {
    let shouldPrepareProductionDeployment: boolean = this.buildConfig.production;
    if (!this.taskConfig.deployCdnPath && this.buildConfig.production) {
      shouldPrepareProductionDeployment = false;
      // Suppressed warning for now to unblock CI. VSO#219493
      this.log/*Warning*/('No deploy CDN path specified. Unable to prepare production deployment.');
    }

    const webpackStats: webpack.compiler.Stats = this.properties.initialWebpackStats || this.properties.webpackStats;
    if (!webpackStats) {
      this.logWarning('Missing webpack stats object. Nothing to prepare for deployment.');
      completeCallback();
      return;
    }

    const bundleEntries: IBundleEntry[]  = this.properties.bundleEntries;
    if (!bundleEntries || !bundleEntries.length || bundleEntries.length === 0) {
      // @TODO: SPPPlat VSO 221536
      // this.logWarning('Missing config entries list. Nothing to prepare for deployment.');
      completeCallback();
      return;
    }

    const tasks: NodeJS.ReadWriteStream[] = [];

    const webpackEntryChunks: Map<string, IWebpackChunk> = new Map<string, IWebpackChunk>();
    const externalModules: IExtendedWebpackModule[] = [];

    const webpackStatsJson: IWebpackStatsObject = webpackStats.toJson();
    const {
      assets: webpackAssets,
      chunks: webpackChunks,
      modules: webpackModules
    }: IWebpackStatsObject  = webpackStatsJson;

    webpackChunks.forEach((webpackChunk: IWebpackChunk) => {
      if (webpackChunk.entry) {
        webpackChunk.names.forEach((name: string) => {
          webpackEntryChunks.set(name, webpackChunk);
        });
      }
    });

    const configExternals: { [name: string]: string | INonAMDExternalModuleConfig } = this.properties.externals || {};

    webpackModules.forEach((webpackModule: IExtendedWebpackModule) => {
      const matches: RegExpMatchArray = webpackModule.name.match(externalsRegex);
      if (matches) {
        const externalName: string = matches[1];
        const configExternal: string | INonAMDExternalModuleConfig = configExternals[externalName];
        const linkedExternal: IExternalProject = (this.properties.linkedExternals || {})[externalName];
        let configExternalPath: string;

        if (configExternal) {
          if (linkedExternal) {
            this.logError(`External "${externalName}" is specified both as a linked package in the node_modules ` +
                          'dependency tree and as an external in in the config.json file. Remove one of the external ' +
                          'references. Only one external reference type is allowed per external package.');
            return;
          } else {
            if (typeof configExternal === 'string') {
              // it is an AMD external
              configExternalPath = configExternal as string;
            } else {
              // it is a non-AMD external
              configExternalPath = configExternal.path;

              if (!configExternal.globalName) {
                this.logError(`External "${externalName}" is defined as a Non-AMD script,` +
                  `but is missing a globalName property.`);
                return;
              }

              webpackModule.nonAmdConfig = configExternal as INonAMDExternalModuleConfig;

              (configExternal.globalDependencies || []).forEach((nonAMDDependency: string) => {
                const dependencyConfig: INonAMDExternalModuleConfig =
                  configExternals[nonAMDDependency] as INonAMDExternalModuleConfig;

                if (!dependencyConfig) {
                  this.logError(`External "${externalName}" is referencing dependency "${nonAMDDependency}" which is ` +
                                'not defined.');
                  return;
                } else if (!dependencyConfig.globalName) {
                  this.logError(`External "${externalName}" is referencing dependency "${nonAMDDependency}", ` +
                                'which is not defined as a global, non-AMD external.');
                  return;
                } else if (this.properties.bundledExternals &&
                          this.properties.bundledExternals.indexOf(nonAMDDependency) !== -1) {
                  this.logError(`External "${externalName}" is referencing dependency "${nonAMDDependency}", which ` +
                                'has been bundled.');
                  return;
                }
              });
            }
          }
        }

        if (configExternalPath) {
          // Keep the whole path if it is a full URL
          webpackModule.isExternalUrl = isUrl(configExternalPath);

          const configExternalFullPath: string = webpackModule.isExternalUrl
              ? configExternalPath : path.join(this.buildConfig.rootPath, configExternalPath);

          webpackModule.configExternalFullPath = configExternalFullPath;
        } else if (!this.properties.discoveredLocalizedResources[externalName] &&
                   !linkedExternal) {
          // Make sure if we haven't found other references to this external, it's been discovered as a localized
          // external
          this.logError(`External "${externalName}" is not correctly referenced in config.json. Deployed ` +
                        'modules will produce unexpected behavior.');
        }

        if (!this.properties.bundledExternals || this.properties.bundledExternals.indexOf(externalName) === -1) {
          // Only record the external it if hasn't been bundled by a second webpack run
          webpackModule.externalName = externalName; // Add the external name as a new property
          webpackModule.renameCallbacks = []; // Create a list of callbacks here
          externalModules.push(webpackModule);
        }
      }
    });

    // Copy non-entry assets to the CDN deploy directory
    if (shouldPrepareProductionDeployment) {
      const assetsToCopy: string[] = [];
      for (const webpackAsset of webpackAssets) {
        const notIgnoredExt: boolean = !this.taskConfig.extsToIgnore ||
          this.taskConfig.extsToIgnore.reduce((state: boolean, ext: string) =>
            state && webpackAsset.name.slice(-ext.length) !== ext, true);
        const notEntryChunk: boolean = webpackAsset.chunks.reduce((state: boolean, chunkId: number) =>
            state && !webpackChunks[chunkId].entry, true);

        if (notIgnoredExt && notEntryChunk) {
            // If we make it here, we aren't ignoring this chunk's extension and it isn't an entrypoint asset
            // so we should copy it to the CDN deploy directory

            // Try to ensure the file has a checksum in the filename
            let checksumFound: boolean = false;
            if (webpackAsset.chunks.length > 0) {
              // Look through all of the asset's chunks and ensure the asset's name contains one of the chunks' hashes
              checksumFound = webpackAsset.chunks.reduce((state: boolean, chunkId: number) =>
                state || webpackAsset.name.indexOf(webpackChunks[chunkId].hash) > -1 , false);
            } else {
              // Split the filename by "-" and "_", and test each part to see if it looks like a 5+ character hex digest
              const name: string = webpackAsset.name;
              const filenameParts: string[] = path.basename(name, path.extname(name)).split(/[-_]/);
              checksumFound = filenameParts.reduce((state: boolean, filenamePart: string) =>
                state || !!filenamePart.match(/[a-fA-F0-9]{5,}/), false);
            }

            if (!checksumFound) {
              this.logError(`Webpack-produced asset "${webpackAsset.name}" does not appear to have a checksum ` +
                            'in its filename, meaning that it is not safe to deploy to the CDN.');
            }

            const assetPath: string = path.join(this.outputDirectory, webpackAsset.name);
            assetsToCopy.push(assetPath);

            if (webpackAsset.name.toLowerCase() !== webpackAsset.name) {
              this.logWarning(`Asset "${webpackAsset.name}"'s name is not all lowercase and will likely not be ` +
                              'accessible after being copied to the CDN. Ensure that its name is all lowercase, ' +
                              'and consider using the "@microsoft/loader-cased-file" loader.');
            }
        }
      }

      // Copy the assets we've queued
      tasks.push(gulp.src(assetsToCopy).pipe(gulp.dest(this.taskConfig.deployCdnPath)));
    }

    const deployEntries: IDeployEntry[] = [];

    const stringsCopyCallbackMap: Map<string, ((locale: string, filename: string) => void)[]> =
              new Map<string, ((locale: string, filename: string) => void)[]>();

    // If we have a bundleTemporaryName, we need to create a regex to find it and replace it in the
    //  "define(<name>, ..." statement
    let bundleTemporaryNameRegex: RegExp = undefined;
    if (this.properties.webpackLibraryNameIsTemporary) {
      const escapedBundleTemporaryName: string = escapeRegExp(this.properties.webpackLibraryName);
      // If we change the type of bundle we're producing, we need to update this
      bundleTemporaryNameRegex = new RegExp(`^(define\\((\\"|\\'))(?:${escapedBundleTemporaryName})((?:\\2)\,)`);
    }

    bundleEntries.forEach((bundleEntry: IBundleEntry) => {
      const manifest: IClientSideComponentManifest = this.getManifest(bundleEntry.configEntry.manifest);
      if (!manifest) {
        this.logWarning(`Missing manifest for entry "${bundleEntry.configEntry.entry}". ` +
                        'Unable to prepare deployment for this entry.');
        return;
      }

      // If we're only producing one manifest in this project, set the version to the package.json version
      if (bundleEntries.length === 1) {
        const manifestVersion: string = this.getPackageVersion();
        manifest.version = manifestVersion;
      }

      const deployEntry: IDeployEntry = {
        bundleEntry: bundleEntry,
        configEntry: bundleEntry.configEntry,
        manifest: manifest,
        entryName: bundleEntry.entryName
      };
      deployEntries.push(deployEntry);

      deployEntry.debugModules = {
        localizedPathModules: new Map<string, ILocalizedPathModuleConfiguration>(),
        pathModules: new Map<string, IPathModuleConfiguration>(),
        componentModules: new Map<string, IComponentModuleConfiguration>()
      };
      deployEntry.productionModules = {
        localizedPathModules: new Map<string, ILocalizedPathModuleConfiguration>(),
        pathModules: new Map<string, IPathModuleConfiguration>(),
        componentModules: new Map<string, IComponentModuleConfiguration>()
      };

      const manifestId: string = deployEntry.manifest.id;
      const stringsModuleId: string = `${manifestId}_strings`;

      this._processEntry(deployEntry,
                         shouldPrepareProductionDeployment,
                         stringsModuleId,
                         bundleTemporaryNameRegex,
                         tasks);

      const entryWebpackChunk: IWebpackChunk = webpackEntryChunks.get(bundleEntry.entryName);
      const webpackChunkId: number = entryWebpackChunk.id;
      this._processExternalsForEntry(webpackChunkId,
                                     deployEntry,
                                     externalModules,
                                     stringsModuleId,
                                     shouldPrepareProductionDeployment,
                                     stringsCopyCallbackMap);
    });

    this._copyStrings(stringsCopyCallbackMap, tasks);

    this._processExternals(externalModules, tasks);

    // Save the deploy entries so they can be picked up by the WriteManifests task.
    this.properties.deployEntries = deployEntries;

    return merge(tasks).on('finish', () => completeCallback());
  }

  private _copyStrings(stringsCopyCallbackMap: Map<string, ((locale: string, filename: string) => void)[]>,
                       tasks: NodeJS.ReadWriteStream[]): void {
    const moduleBaseName: string = path.basename(this.buildConfig.rootPath).toLowerCase();

    stringsCopyCallbackMap.forEach((callbacks: ((locale: string, filename: string) => void)[], key: string) => {
      const isStandardStringsModule: boolean = key === constants.stringsExternalBundleName;
      const baseFilename: string = isStandardStringsModule
                                    ? moduleBaseName
                                    : `${moduleBaseName}-${key.toLowerCase().replace(/\//g, '_')}`;

      const localizedFiles: ILocaleMap = this.properties.discoveredLocalizedResources[key];
      for (const locale in localizedFiles) {
        if (localizedFiles.hasOwnProperty(locale)) {
          tasks.push(this._renameWithHash(gulp.src(localizedFiles[locale]),
                                          (hash: string) => `${baseFilename}_${locale}_${hash}.js`,
                                          (filename: string) =>
                                            callbacks.forEach((callback: (locale: string, filename: string) => void) =>
                                              callback(locale, filename)))
                         .pipe(gulp.dest(this.taskConfig.deployCdnPath)));
        }
      }
    });
  }

  private _processEntry(deployEntry: IDeployEntry,
                        shouldPrepareProductionDeployment: boolean,
                        stringsModuleId: string,
                        bundleTemporaryNameRegex: RegExp,
                        tasks: NodeJS.ReadWriteStream[]): boolean {
    const bundleEntry: IBundleEntry = deployEntry.bundleEntry;
    if (bundleEntry.nonLocalizedEntrypointPath && bundleEntry.localizedEntrypointPaths) {
      this.logError('Both localized and non-localized entrypoint files are specified for ' +
                    `entry ${deployEntry.configEntry.entry}. Only one may be specified.`);
      return false;
    }

    const entryName: string = deployEntry.entryName;
    const entryRegExp: string = `${entryName}_?[a-zA-Z0-9-_]*\.js`;

    let entrypointFilesGlob: string[];
    let productionRenameCallback: (filename: string, originalFilename: string) => void;
    let getFilenameCallback: (filename: string, originalFilename?: string) => string;

    if (bundleEntry.nonLocalizedEntrypointPath) {
      const entryBundlePath: string = bundleEntry.nonLocalizedEntrypointPath;

      entrypointFilesGlob = [entryBundlePath];

      // Record the debug entry module
      const debugModules: IDeployModules = deployEntry.debugModules;
      const debugEntryModule: IPathModuleConfiguration = {
        type: 'path',
        path: entryBundlePath
      };
      debugModules.pathModules.set(entryName, debugEntryModule);
      debugModules.entryModuleId = entryName;

      if (shouldPrepareProductionDeployment) {
        // Record the production entry module
        const productionModules: IDeployModules = deployEntry.productionModules;
        const productionEntryModule: IPathModuleConfiguration = {
          type: 'path',
          path: undefined
        };
        productionModules.pathModules.set(entryName, productionEntryModule);
        productionModules.entryModuleId = entryName;

        productionRenameCallback = (filename: string) => productionEntryModule.path = filename;
        getFilenameCallback = (hash: string) => `${entryName.toLowerCase()}_${hash}.js`;
      }
    } else if (bundleEntry.localizedEntrypointPaths) {
      const reverseEntrypointLookup: { [filePath: string]: string } = {};
      entrypointFilesGlob = [];

      for (const locale in bundleEntry.localizedEntrypointPaths) {
        if (bundleEntry.localizedEntrypointPaths.hasOwnProperty(locale)) {
          const path: string = bundleEntry.localizedEntrypointPaths[locale];
          reverseEntrypointLookup[path] = locale;
          entrypointFilesGlob.push(path);
        }
      }

      const defaultLocale: IDefaultLocale =
        this._selectDefaultLocaleForResource(bundleEntry.localizedEntrypointPaths, entryName);

      // Record the debug entry module
      const debugModules: IDeployModules = deployEntry.debugModules;
      const debugEntryModule: ILocalizedPathModuleConfiguration = {
        type: 'localizedPath',
        paths: {},
        defaultPath: undefined
      };
      debugModules.localizedPathModules.set(entryName, debugEntryModule);
      debugModules.entryModuleId = entryName;

      this._fillLocalizedPaths(bundleEntry.localizedEntrypointPaths, defaultLocale, debugEntryModule);

      if (shouldPrepareProductionDeployment) {
        // Record the production entry module
        const productionModules: IDeployModules = deployEntry.productionModules;
        const productionEntryModule: ILocalizedPathModuleConfiguration = {
          type: 'localizedPath',
          paths: {},
          defaultPath: undefined
        };
        productionModules.localizedPathModules.set(entryName, productionEntryModule);
        productionModules.entryModuleId = entryName;

        productionRenameCallback = (filename: string, originalFilename: string) => {
          const locale: string = reverseEntrypointLookup[originalFilename];
          this._fillLocalizedPath(filename, locale, defaultLocale, productionEntryModule);
        };

        getFilenameCallback = (hash: string, originalFilename: string) => {
          const locale: string = reverseEntrypointLookup[originalFilename];
          return `${entryName.toLowerCase()}_${locale}_${hash}.js`;
        };
      }
    } else {
      this.logError('Neither a localized entrypoint file nor non-localized entrypoint files are specified for ' +
                    `entry ${deployEntry.configEntry.entry}. One must be specified.`);
      return false;
    }

    // Initialize the entry gulp stream and make a few initial transformations
    let entryGulpStream: NodeJS.ReadWriteStream =
          gulp.src(entrypointFilesGlob)
              .pipe(through2.obj(this._insertGlobalRegisterCodeIfNecessary(this, !this.buildConfig.production)))
              .pipe(gulpReplace(defaultStringsModuleRegex, `$1${stringsModuleId}$3`))
              .pipe(gulpReplace(new RegExp(constants.defaultWebpackPublicPathScriptName, 'g'), entryRegExp));

    if (bundleTemporaryNameRegex) {
      // If we have a bundle temporary name, we need to set the correct name for each entrypoint file
      const correctName: string = getBundleId(bundleEntry.manifest);
      entryGulpStream = entryGulpStream.pipe(gulpReplace(bundleTemporaryNameRegex, `$1${correctName}$3`));
    }

    // Write entry file(s) back to 'dist'
    entryGulpStream = entryGulpStream.pipe(gulp.dest(this.outputDirectory));

    if (shouldPrepareProductionDeployment) {
      // Write production file(s) to the CDN deploy path
      entryGulpStream = this._renameWithHash(entryGulpStream, getFilenameCallback, productionRenameCallback)
                            .pipe(gulp.dest(this.taskConfig.deployCdnPath));
    }

    tasks.push(entryGulpStream);
  }

  private _processExternals(externalModules: IExtendedWebpackModule[], tasks: NodeJS.ReadWriteStream[]): void {
    externalModules.forEach((externalModule: IExtendedWebpackModule) => {
      if (externalModule.renameCallbacks.length > 0) {
        // Replace slashes in the filename with dashes so we don't end up with unexpected subfolders
        const baseFilename: string = externalModule.externalName.replace(/[\/\\]/, '-');
        tasks.push(this._renameWithHash(gulp.src(externalModule.configExternalFullPath),
                                        (hash: string) => `${baseFilename.toLowerCase()}_${hash}.js`,
                                        (filename: string) =>
                                          externalModule.renameCallbacks.forEach(
                                            (renameCallback: (filename: string) => void) =>
                                              renameCallback(filename)))
                        .pipe(gulp.dest(this.taskConfig.deployCdnPath)));
      }
    });
  }

  private _processExternalsForEntry(webpackChunkId: number,
                                    deployEntry: IDeployEntry,
                                    externalModules: IExtendedWebpackModule[],
                                    stringsModuleId: string,
                                    shouldPrepareProductionDeployment: boolean,
                                    stringsCopyCallbackMap: Map<string,
                                                                ((locale: string, filename: string) => void)[]>): void {
    externalModules.forEach((externalModule: IExtendedWebpackModule) => {
      // Determine if this external is referenced by the entry chunk
      if (externalModule.chunks.reduce((state: boolean, chunkId: number) => state || webpackChunkId === chunkId,
                                        false)) {
        const externalName: string = externalModule.externalName;

        const localizedResource: ILocaleMap =  this.properties.discoveredLocalizedResources[externalName];
        if (localizedResource) {
          // This is a localized resource
          const isStandardStringsModule: boolean = externalName === constants.stringsExternalBundleName;
          const resourceName: string = isStandardStringsModule ? stringsModuleId : externalName;

          const debugModule: ILocalizedPathModuleConfiguration = {
            defaultPath: undefined,
            type: 'localizedPath',
            paths: {}
          };
          deployEntry.debugModules.localizedPathModules.set(resourceName, debugModule);

          const defaultLocale: IDefaultLocale = this._selectDefaultLocaleForResource(localizedResource, resourceName);

          this._fillLocalizedPaths(localizedResource, defaultLocale, debugModule);

          if (shouldPrepareProductionDeployment) {
            const productionModule: ILocalizedPathModuleConfiguration = {
              defaultPath: undefined,
              type: 'localizedPath',
              paths: {}
            };
            deployEntry.productionModules.localizedPathModules.set(resourceName, productionModule);

            if (!stringsCopyCallbackMap.has(externalName)) {
              stringsCopyCallbackMap.set(externalName, []);
            }

            const callbacks: ((locale: string, filename: string) => void)[] = stringsCopyCallbackMap.get(externalName);
            callbacks.push(((locale: string, filename: string): void => {
              this._fillLocalizedPath(filename, locale, defaultLocale, productionModule);
            }));
          }
        } else if ((this.properties.linkedExternals || {})[externalName]) {
          // This is a component module, so we need to record that this entry is using it, but we don't need to
          // copy it to the CDN directory

          const linkedExternal: IExternalProject = this.properties.linkedExternals[externalName];

          const debugModule: IComponentModuleConfiguration = {
            type: 'component',
            version: linkedExternal.version,
            id: linkedExternal.id,
            failoverPath: linkedExternal.failoverPath
          };
          deployEntry.debugModules.componentModules.set(externalName, debugModule);

          if (shouldPrepareProductionDeployment) {
            const productionModule: IComponentModuleConfiguration = {
              type: 'component',
              version: linkedExternal.version,
              id: linkedExternal.id
            };
            deployEntry.productionModules.componentModules.set(externalName, productionModule);
          }
        } else if (!externalModule.configExternalFullPath) {
          this.logError(`External "${externalName}" was specified without a path, and it is neither a ` +
                        'component nor a localized module. The produced manifest will likely not load.');
          return;
        } else {
          // This is a plain path module, so we do need to copy it to the CDN directory if we're in production
          // mode and it is not a fully qualified URL

          deployEntry.debugModules.pathModules.set(externalName, externalModule.nonAmdConfig ?
            {
              type: 'path',
              path: externalModule.configExternalFullPath,
              globalDependencies: externalModule.nonAmdConfig.globalDependencies,
              globalName: externalModule.nonAmdConfig.globalName
            } : {
              type: 'path',
              path: externalModule.configExternalFullPath
            });

          if (shouldPrepareProductionDeployment) {
            const productionModule: IPathModuleConfiguration = externalModule.nonAmdConfig ?
              {
                type: 'path',
                path: undefined,
                globalDependencies: externalModule.nonAmdConfig.globalDependencies,
                globalName: externalModule.nonAmdConfig.globalName
              } : {
                type: 'path',
                path: undefined
              };
            deployEntry.productionModules.pathModules.set(externalName, productionModule);

            if (externalModule.isExternalUrl) {
              // There won't be a rename since this is not on the local filesystem
              productionModule.path = externalModule.configExternalFullPath;
            } else {
              externalModule.renameCallbacks.push((filename: string) => productionModule.path = filename);
            }
          }
        }
      }
    });
  }

  private _renameWithHash(gulpStream: NodeJS.ReadWriteStream,
                          getFilename: (hash: string, originalFilename: string) => string,
                          filenameCallback: (filename: string, originalFilename: string) => void):
                            NodeJS.ReadWriteStream {
    return gulpStream.pipe(through2.obj(this._getRenameTransformFunction(getFilename, filenameCallback)));
  }

  private _getRenameTransformFunction(getFilename: (hash: string, originalFilename: string) => string,
                                      filenameCallback: (filename: string, originalFilename: string) => void):
                                        (file: gulpUtil.File, enc: string, callback: () => void) => void {
    const self: CopyAssetsTask = this;
      return function (file: gulpUtil.File, enc: string, callback: () => void): void {
        const checksum: string = self.getChecksum((file.contents as Buffer).toString(enc));

        const originalFilename: string = file.path;
        const filename: string = getFilename(checksum, originalFilename);
        file.path = path.join(path.dirname(file.path), filename);

        filenameCallback(filename, originalFilename);

        this.push(file);

        callback();
      };
  }

  private _selectDefaultLocaleForResource(paths: ILocaleMap, externalName: string): IDefaultLocale {
    let defaultLocale: string;
    let defaultLocaleInPaths: boolean = false;
    const localeKeys: string[] = Object.keys(paths);
    if (localeKeys.length === 1) {
      // We only have one locale, so pick that one as the default
      defaultLocale = localeKeys[0];
    } else if ('default' in paths) {
      // We have a "default" default locale, so make "default" the default
      defaultLocale = 'default';
    } else if (constants.defaultLocale in paths) {
      // We have the constants.defaultLocale locale ("en-us"), so pick that one as the default
      defaultLocale = constants.defaultLocale;
      defaultLocaleInPaths = true; // In this case, the default locale is en-us, but that's also a regular path
    } else {
      // We couldn't pick a default locale, so issue a warning
      this.logWarning(`Unable to select a default locale for external resource "${externalName}". ` +
                      'Packaging a production build will fail.');
    }

    return { defaultLocale, defaultLocaleInPaths };
  }

  private _fillLocalizedPaths(paths: ILocaleMap,
                              defaultLocale: IDefaultLocale,
                              moduleConfig: ILocalizedPathModuleConfiguration): void {
    for (const locale in paths) {
      if (paths.hasOwnProperty(locale)) {
        const path: string = paths[locale];
        this._fillLocalizedPath(path, locale, defaultLocale, moduleConfig);
      }
    }
  }

  private _fillLocalizedPath(path: string,
                             locale: string,
                             defaultLocale: IDefaultLocale,
                             moduleConfig: ILocalizedPathModuleConfiguration): void {
    const isDefaultLocale: boolean = locale === defaultLocale.defaultLocale;
    if (isDefaultLocale) {
      moduleConfig.defaultPath = path;
    }

    if (!isDefaultLocale || defaultLocale.defaultLocaleInPaths) {
      moduleConfig.paths[locale] = path;
    }
  }

  /**
   * For webpack entries that are imported using SystemJS, the script is injected into the page, but
   * the element is removed before the entry is initialized. Webpack requires a public path be set,
   * and because we don't have a single host location, we need to set the public path dynamically.
   * The public path is set when the entry is initialized, and in certain cases (like in development),
   * it will look at the script tags on the page to determine what the root path is.
   *
   * We can append a small bit of code to the end of the entry's JS file that will mitigate this issue
   * by maintaining a registry of scripts that have been injected onto the page, so the public path
   * can be correctly set even if the script element that pointed to the entry's JS file has been removed
   * already.
   */
  private _insertGlobalRegisterCodeIfNecessary(self: CopyAssetsTask, debug: boolean):
      (file: gulpUtil.File, enc: string, callback: () => void) => void {
    return function(file: gulpUtil.File, enc: string, callback: () => void): void {
      if (!file.isNull() && file.contents) {
        const contents: string = (file.contents as Buffer).toString(enc);
        if (contents.match(new RegExp(setWebpackPublicPath.registryVarName, 'g'))) {
          const registerCode: string = setWebpackPublicPath.getGlobalRegisterCode(debug);
          file.contents = new Buffer(`${contents}${registerCode}`);
        }

        this.push(file);
      }

      callback();
    };
  }
}
